import axios from '@/utils/uniAxios.js';
import {  
    mapMutations  
} from 'vuex';
export default {
	data() {
		return {
			person:{
				avartar:'/static/missing-face.png',
				userName:'涂志奇',
				sex:'男',
				birthday:'1994-04-21',
				weight:'74KG',
			},
			id:''
		};
	},
	onLoad(option){
		this.id = option.id;
		this.getData()
	},
	methods:{
		...mapMutations(['logout']),
		// 数据初始化
		getData(){
			let obj ={
				id:this.id
			}
			// 如果是手机号码直接登录，不填
			if(uni.getStorageSync('setPhone')){
				axios.post('/sso/user/id',obj).then(res=>{
					// this.person = res.data.data;
					this.person = {
						avartar:'',
						userName:'',
						sex:'',
						birthday:'',
						weight:'',
					}
				})
			}
		},
		navTo(url){
			// this.$api.msg(`跳转到${url}`);
		},
		//保存
		save(){
			// 返回上一页
			uni.navigateBack()
		},
	}
}
